.movie{
    background-color: #fff;
    margin-bottom: 70px;
    font-weight: 300;
    padding: 20px;
    border-radius: 5px;
    color: #adaeb9;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25), 0 8px 16px -8px rgba(0, 0, 0, .3);
    a{color: #2c2c2c;text-decoration: none;}
    img{
        position: relative;
        top: -50px;
        max-width: 230px;
        width: 100%;
        margin-right: 30px;
        box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25), 0 18px 36px -18px rgba(0, 0, 0, .3);
    }
    .movie_data{
        .movie_title{font-weight: 300;margin: 0;margin-bottom: 5px;font-size: 24px;color: #2c2c2c;}
        .movie_year{font-weight: 300;margin: 0;font-size: 14px;margin-right: 10px;}
        .movie_genres{
            list-style: none;
            padding: 0;
            margin: 0;
            display: flex;
            flex-wrap: wrap;
            margin: 5px 0px;
            .movie_genre{
                margin-right: 20px;
                position: relative;
                &:not(:last-child):after{
                    content:"";
                    width: 1px;
                    height: 10px;
                    background: #adeab9;
                    position: absolute;
                    top: 8px;
                    right: -10px;
                }
            }
        }
        .movie_summary{}
    }
}